<template>
    <div class="house">
        <img :src="require('@/assets/newImages/winter/'+ branch +'/'+houseNumber+'/house.png')" alt="" class="house-background">
        <div class="house-header" v-if="dataReady">
            <p class="title">{{ $localizeFilter('house_entrance_1') + houseNumber + $localizeFilter('house_entrance_2') + housesConditions[branch][houseNumber-1].joinFee +' dream' }}</p>
        </div>

        <div class="search"> 
            <input type="text" v-model.trim="searchText" class="custom-input" :placeholder="$localizeFilter('partner_login')">
            <button class="search-button" @click="search()"><img class="search-icon" src="@/assets/newImages/search_icon.png"></button>
            <HouseSearchDropdown v-if="isDropdownOpen" :options="options" @selectOption="selectOption" :loginNotFound="loginNotFound"/>
        </div>

        <div class="house-header-buttons" :class="canJoin ? '' : 'reverse'">
            <button class="orange-button" v-if="canJoin" @click="showModalJoinHouse=true" :disabled="houseJoinInProgress">{{$localizeFilter('house_join_project')}}</button>
            <button class="orange-button" v-if="canPutCloneAuto" @click="showModalPutCloneAuto=true" :disabled="putCloneAutoInProgress">{{$localizeFilter('house_put_clone_auto')}}</button>
            <button class="orange-button" v-if="canPop" @click="popMatrixUser()">{{$localizeFilter('house_step_back')}}</button>
            <button class="orange-button" v-if="canReturn" @click="loadData()">{{$localizeFilter('house_return_back')}}</button>
        </div>

        

        <div class="top-window" v-if="dataReady">
            <!-- <img class="avatar" src="@/assets/newImages/chelik.png" alt=""> -->
            <AvatarPlace v-if="top" class="avatar" :avatarLink="topParams.avatarLink || require('@/assets/newImages/empty_avatar.png')" :isLockedUntilTimestamp="topParams.isLockedUntilTimestamp" :matrixUserId="top?.id" :side="'top'" />
            <div v-if="top" class="nickname">{{ top.nickname + (top.number>1 ? ' #' + top.number : '') }}</div>
        </div>
        <div class="left-window" v-if="dataReady">
            <!-- <img class="avatar" src="@/assets/newImages/chelik.png" alt=""> -->
            <AvatarPlace v-if="top" class="avatar" :avatarLink="leftParams.avatarLink || require('@/assets/newImages/empty_avatar.png')" :isLockedUntilTimestamp="leftParams.isLockedUntilTimestamp" :matrixUserId="left?.id" :side="'left'" @pushMatrixUser="pushMatrixUser" @putClone="tryPutClone('left')" @lockPlace="tryLockPlace('left')" @unlockPlace="tryUnlockPlace('left')" />
            <div v-if="left" class="nickname">{{ left.nickname + (left.number>1 ? ' #' + left.number : '') }}</div>
        </div>
        <div class="right-window" v-if="dataReady">
            <!-- <img class="avatar" src="@/assets/newImages/chelik.png" alt=""> -->
            <AvatarPlace v-if="top" class="avatar" :avatarLink="rightParams.avatarLink || require('@/assets/newImages/empty_avatar.png')" :isLockedUntilTimestamp="rightParams.isLockedUntilTimestamp" :matrixUserId="right?.id" :side="'right'" @pushMatrixUser="pushMatrixUser" @putClone="tryPutClone('right')" @lockPlace="tryLockPlace('right')" @unlockPlace="tryUnlockPlace('right')" />
            <div v-if="right" class="nickname">{{ right.nickname + (right.number>1 ? ' #' + right.number : '') }}</div>
        </div>


        <template v-if="branch=='master'">
            <div v-if="houseNumber%2==0 && housesConditions[branch][houseNumber-1].bounty.toBalanceLeft" class="house-footer full">
                <p class="bounty" v-if="houseNumber%2==0"><span v-if="housesConditions[branch][houseNumber-1].bounty.toBalanceLeft">{{$localizeFilter('house_to_balance') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toBalanceLeft +' dream'}}</span><br v-if="housesConditions[branch][houseNumber-1].bounty.toRealPropertyLeft"><span v-if="housesConditions[branch][houseNumber-1].bounty.toRealPropertyLeft">{{$localizeFilter('house_to_real_property') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toRealPropertyLeft +' dream'}}</span></p>
                <p class="bounty" v-if="houseNumber%2==0"><span v-if="housesConditions[branch][houseNumber-1].bounty.toBalanceRight">{{$localizeFilter('house_to_balance') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toBalanceRight +' dream'}}</span><br v-if="housesConditions[branch][houseNumber-1].bounty.toRealPropertyRight"><span v-if="housesConditions[branch][houseNumber-1].bounty.toRealPropertyRight">{{$localizeFilter('house_to_real_property') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toRealPropertyRight +' dream'}}</span></p>
            </div>

            <div v-else class="house-footer" >
                <!-- <button class="orange-button" v-if="canJoin" @click="joinHouse()">{{$localizeFilter('house_join_project')}}</button> -->
                <p class="bounty" v-if="houseNumber%2==0">{{ $localizeFilter('house_to_balance') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toBalance +' dream'}}<br v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor"><span v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor">{{$localizeFilter('house_to_sponsor') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toSponsor +' dream'}}</span><br v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor"></p>
                <p class="bounty" v-if="houseNumber%2==0">{{ $localizeFilter('house_to_balance') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toBalance +' dream'}}<br v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor"><span v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor">{{$localizeFilter('house_to_sponsor') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toSponsor +' dream'}}</span><br v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor"></p>
            </div>
        </template>

        <template v-else>
            <div class="house-footer full" >
                <p class="bounty" v-if="housesConditions[branch][houseNumber-1].bounty">
                    {{ $localizeFilter('house_to_balance') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toBalance +' dream'}}
                    <br v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor">
                    <span v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor">{{$localizeFilter('house_to_sponsor') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toSponsor +' dream'}}</span>
                    <br v-if="housesConditions[branch][houseNumber-1].bounty.cloneToFirstHouseLeft">
                    <span v-if="housesConditions[branch][houseNumber-1].bounty.cloneToFirstHouseLeft">{{$localizeFilter('clone_to_first_house')}}</span>
                    <br v-if="housesConditions[branch][houseNumber-1].bounty.cloneToBranchLeft">
                    <span v-if="housesConditions[branch][houseNumber-1].bounty.cloneToBranchLeft">{{$localizeFilter('clone_to_branch', [housesConditions[branch][houseNumber-1].bounty.cloneToBranchLeft.branch, housesConditions[branch][houseNumber-1].bounty.cloneToBranchLeft.houseNumber])}}</span>
                </p>
                <p class="bounty" v-if="housesConditions[branch][houseNumber-1].bounty">
                    {{ $localizeFilter('house_to_balance') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toBalance +' dream'}}
                    <br v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor">
                    <span v-if="housesConditions[branch][houseNumber-1].bounty.toSponsor">{{$localizeFilter('house_to_sponsor') + ' - '+ housesConditions[branch][houseNumber-1].bounty.toSponsor +' dream'}}</span>
                    <br v-if="housesConditions[branch][houseNumber-1].bounty.cloneToFirstHouseRight">
                    <span v-if="housesConditions[branch][houseNumber-1].bounty.cloneToFirstHouseRight">{{$localizeFilter('clone_to_first_house')}}</span>
                    <br v-if="housesConditions[branch][houseNumber-1].bounty.cloneToBranchRight">
                    <span v-if="housesConditions[branch][houseNumber-1].bounty.cloneToBranchRight">{{$localizeFilter('clone_to_branch', [housesConditions[branch][houseNumber-1].bounty.cloneToBranchRight.branch, housesConditions[branch][houseNumber-1].bounty.cloneToBranchRight.houseNumber])}}</span>
                </p>
            </div>
        </template>

        <!-- <Teleport to=".app-page.cabinet" :disabled="!dataReady"> -->
        <Teleport to="#app" :disabled="!dataReady">
            <Modal v-if="showModalJoinHouse" :type="'confirmAction'" :actionType="'joinHouse'" @closeModal="closeJoinHouseModal"/>
            <Modal v-if="showModalPutCloneAuto" :type="'confirmAction'" :actionType="'putCloneAuto'" @closeModal="closePutCloneAutoModal"/>
            <Modal v-if="showModalPutClone" :type="'confirmAction'" :actionType="'putClone'" @closeModal="closePutCloneModal"/>
            <Modal v-if="showModalLockPlace" :type="'confirmAction'" :actionType="'lockPlace'" @closeModal="closeLockPlaceModal"/>
            <Modal v-if="showModalUnlockPlace" :type="'confirmAction'" :actionType="'unlockPlace'" @closeModal="closeUnlockPlaceModal"/>
            <Modal v-if="showModalPutCloneForceLoan" :type="'confirmAction'" :actionType="'putCloneForceLoan'" :loanAvailableAmount="loanAvailableAmount" @closeModal="closePutCloneForceLoanModal"/>
        </Teleport>

    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from "@/filters/localize.filter";
import AvatarPlace from '@/components/AvatarPlace';
import Modal from "@/components/modals/Modal.vue"
import HouseSearchDropdown from '@/components/HouseSearchDropdown.vue';

export default defineComponent({
  props: {
      houseNumber: {
          type: Number,
          default: 1
      },
      branch: {
          type: String,
          default: 'master'
      }
  },
  watch: {
      houseNumber: async function(newVal, oldVal){
          await this.loadData();
      }
  },
  components: {
      AvatarPlace, Modal, HouseSearchDropdown
  },
  data: () => ({
      showModalJoinHouse: false,
      showModalPutClone: false,
      showModalLockPlace: false,
      showModalUnlockPlace: false,
      showModalPutCloneForceLoan: false,
      showModalPutCloneAuto: false,
      loanAvailableAmount: 0,
      dataReady: false,
      side: '',
      top: undefined,
      left: undefined,
      right: undefined,
      root: undefined,
      navigationStack: [],
      housesConditions: {
        master: [
            {
                joinFee: 10
            },
            {
                joinFee: 20,
                bounty: {
                    toBalance: 10/2,
                }
            },
            {
                joinFee: 30
            },
            {
                joinFee: 60,
                bounty: {
                    toBalance: 30/2,
                }
            },
            {
                joinFee: 90
            },
            {
                joinFee: 180,
                bounty: {
                    toBalance: 60/2,
                    toSponsor: 30/2
                }
            },
            {
                joinFee: 270
            },
            {
                joinFee: 540,
                bounty: {
                    toBalance: 200/2,
                    toSponsor: 80/2
                }
            },
            {
                joinFee: 800
            },
            {
                joinFee: 1600,
                bounty: {
                    toBalance: 500/2,
                    toSponsor: 100/2
                }
            },
            {
                joinFee: 2500
            },
            {
                joinFee: 5000,
                bounty: {
                    toBalance: 1600/2,
                    toSponsor: 400/2
                }
            },
            {
                joinFee: 8000
            },
            {
                joinFee: 16000,
                bounty: {
                    toBalance: 5000/2,
                    toSponsor: 2000/2
                }
            },
            {
                joinFee: 25000
            },
            {
                joinFee: 50000,
                bounty: {
                    toRealPropertyLeft: 10000,
                    toRealPropertyRight: 10000,
                    toBalanceLeft: 40000,
                    toBalanceRight: 40000,
                }
            }
        ],
        jump: [
            {
                joinFee: 60
            },
            {
                joinFee: 120,
                bounty: {
                    toBalance: 15,
                }
            },
            {
                joinFee: 210,
                bounty: {
                    toBalance: 120,
                    toSponsor: 30,
                    cloneToBranchRight: {
                        branch: 'EVERY DAY',
                        houseNumber: 4
                    },
                    cloneToFirstHouseLeft: true
                }
            }
        ],
        swift: [
            {
                joinFee: 270
            },
            {
                joinFee: 540,
                bounty: {
                    toBalance: 140,
                }
            },
            {
                joinFee: 800,
                bounty: {
                    toBalance: 430,
                    toSponsor: 100,
                    cloneToBranchRight: {
                        branch: 'EVERY DAY',
                        houseNumber: 7
                    },
                    cloneToFirstHouseLeft: true
                }
            }
        ],
      },
      responses: [],
      searchText: '',
      houseJoinInProgress: false,
      putCloneAutoInProgress: false,
      options: [], //[{name: 'Lol 1', depth: 1}, {name: 'Lol 2', depth: 2}, {name: 'Lol 3', depth: 3}, {name: 'Lol 1', depth: 1}, {name: 'Lol 2', depth: 2}, {name: 'Lol 3', depth: 3}],
      isDropdownOpen: false,
      loginNotFound: false,
  }),
  methods: {
    selectOption(idx) {
        // console.log(this.options[idx])
        this.isDropdownOpen=false
        this.navigationStack = this.responses[idx][1].map((item) => item.root.id)

        let {left, right, root: top} = this.responses[idx][1][this.responses[idx][1].length - 1]
        this.root = this.responses[idx][1][0].root;
        this.top = top;
        this.left = left;
        this.right = right;
    },
    async search() {
        this.loginNotFound=false
        this.isDropdownOpen=true
        // ...
        
        try {
            const data = {houseNumber: this.houseNumber, nickname: this.searchText, branch: this.branch}
            let responses = Object.entries(await this.$store.dispatch('searchInHouseStructureByNickname', data))
            this.loginNotFound = false
            this.options = responses.map(([cloneNumber, chain]) => {
                let nickname = chain[chain.length - 1].root.nickname
                return {name: nickname + (cloneNumber > 1 ? ' #'+cloneNumber : ''), depth: chain.length - 1}
            })
            this.responses = responses
        } catch(e) {
            if(e.message === 'User not found') {
                this.loginNotFound=true
            } else {
                this.$error(e)
            }
        } 

    },
    async loadData(){
        try {
            const data = {houseNumber: this.houseNumber, branch: this.branch}
            let {left, right, root: top} = await this.$store.dispatch('getHouseStructureByClientId', data)
            this.root = top ? JSON.parse(JSON.stringify(top)) : undefined;
            this.top = top;
            this.left = left;
            this.right = right;
            this.dataReady = true;
            if (this.root?.id) {
                this.navigationStack = [this.root.id];
            }
        } catch(e) {
            this.$error(e)
        } 
    },
    async pushMatrixUser(matrixUserId){
        this.navigationStack.push(matrixUserId);
        await this.loadMatrixUser(matrixUserId);
    },
    async popMatrixUser(){
        this.navigationStack.pop();
        let matrixUserId = this.navigationStack[this.navigationStack.length - 1];
        await this.loadMatrixUser(matrixUserId);
    },
    async loadMatrixUser(matrixUserId){
        if (!matrixUserId) {
            return;
        }
        try {
            const data = {houseNumber: this.houseNumber, matrixUserId, branch: this.branch}
            let {left, right, root: top} = await this.$store.dispatch('getHouseStructureByMatrixUserId', data)
            this.top = top;
            this.left = left;
            this.right = right;
            this.dataReady = true;
        } catch(e) {
            this.$error(e)
        } 
    },

    tryPutClone(side){
        this.showModalPutClone = true;
        this.side = side;
    },
    tryLockPlace(side){
        this.showModalLockPlace = true;
        this.side = side;
    },
    tryUnlockPlace(side){
        this.showModalUnlockPlace = true;
        this.side = side;
    },

    closeJoinHouseModal(confirmed){
        this.showModalJoinHouse = false;
        if(confirmed){
            this.joinHouse();
        }
    },

    closePutCloneModal(confirmed){
        this.showModalPutClone = false;
        if(confirmed){
            this.putClone(this.side);
        }
    },

    closePutCloneAutoModal(confirmed){
        this.showModalPutCloneAuto = false;
        if(confirmed){
            this.putCloneAuto();
        }
    },

    closeLockPlaceModal(confirmed){
        this.showModalLockPlace = false;
        if(confirmed){
            this.lockPlace(this.side);
        }
    },

    closeUnlockPlaceModal(confirmed){
        this.showModalUnlockPlace = false;
        if(confirmed){
            this.unlockPlace(this.side);
        }
    },

    closePutCloneForceLoanModal(confirmed){
        this.showModalPutCloneForceLoan = false;
        if(confirmed){
            this.putClone(this.side, true);
        }
    },

    async putClone(side, forceLoan){
        const data = {matrixUserId: this.top.id, side, forceLoan};
        try {
            let {result} = await this.$store.dispatch('putClone', data);
            if (result!==true && result.result=='not_enough_funds' && result.loanAvailableAmount) {
                this.showModalPutCloneForceLoan = true;
                this.loanAvailableAmount = result.loanAvailableAmount;
            }
        } catch (e) {
            this.$error(e)
        } finally {
            await this.loadMatrixUser(this.top.id);
        }
    },

    async putCloneAuto(){
        const data = {houseNumber: this.houseNumber, branch: this.branch};
        this.putCloneAutoInProgress = true
        try {
            let {result} = await this.$store.dispatch('putCloneAuto', data);
            this.putCloneAutoInProgress = false
            // if (result!==true && result.result=='not_enough_funds' && result.loanAvailableAmount) {
            //     this.showModalPutCloneForceLoan = true;
            //     this.loanAvailableAmount = result.loanAvailableAmount;
            // }
        } catch (e) {
            this.$error(e)
            this.putCloneAutoInProgress = false
        } finally {
            await this.loadMatrixUser(this.top.id);
        }
    },

    async lockPlace(side){
        const data = {matrixUserId: this.top.id, side};
        try {
            await this.$store.dispatch('lockPlace', data)
        } catch (e) {
            this.$error(e)
        } finally {
            await this.loadMatrixUser(this.top.id);
        }
    },

    async unlockPlace(side){
        const data = {matrixUserId: this.top.id, side};
        try {
            await this.$store.dispatch('unlockPlace', data)
        } catch (e) {
            this.$error(e)
        } finally {
            await this.loadMatrixUser(this.top.id);
        }
    },

    async joinHouse(){
        this.houseJoinInProgress = true
        try {
            const data = {houseNumber: this.houseNumber, branch: this.branch}
            await this.$store.dispatch('joinHouse', data)
            await this.loadData();
            this.dataReady = true;
            this.houseJoinInProgress = false
        } catch(e) {
            this.$error(e)
            this.houseJoinInProgress = false
        } 
    }
  },
  computed: {
      canJoin() {
          return !this.root;
      },
      canPutCloneAuto() {
        return this.top && this.root && this.top.id == this.root.id
      },
      canPop() {
        return this.top && this.root && this.top.id !== this.root.id
      },
      canReturn() {
        return this.top && this.root && this.top.id !== this.root.id
      },
      topParams(){
          let params = {};
          if (this.top) {
              if (this.top.isLock){
                  params.isLockedUntilTimestamp = this.top.expiresTimestamp;
              } else {
                  params.avatarLink = this.top.avatarLink || require('@/assets/newImages/empty_avatar.png');
              }
          }
          return params;
      },
      leftParams(){
          let params = {};
          if (this.left) {
              if (this.left.isLock){
                  params.isLockedUntilTimestamp = this.left.expiresTimestamp;
              } else {
                  params.avatarLink = this.left.avatarLink || require('@/assets/newImages/empty_avatar.png');
              }
          }
          return params;
      },
      rightParams(){
          let params = {};
          if (this.right) {
              if (this.right.isLock){
                  params.isLockedUntilTimestamp = this.right.expiresTimestamp;
              } else {
                  params.avatarLink = this.right.avatarLink || require('@/assets/newImages/empty_avatar.png');
              }
          }
          return params;
      },
  },
  async mounted() { 
    await this.loadData()
    // window.addEventListener('scroll', this.handleScroll);
  },
  unmounted(){
    //   console.log('kek')
  }
});
</script>

