<template>
    <div :class="'window light dropdown-block ' + (loginNotFound ? 'invalid' : '')">
        <ul v-if="!loginNotFound">
            <li v-for="(option,idx) in options" :key="idx" :class="selectedIndex==idx ? 'selected' : ''" @click="selectOption(idx)">
                {{$localizeFilter(option.name)}}
                <div class="depth">
                    <img src="@/assets/newImages/depth_icon.png" class="depth-icon">
                    <div class="depth-number">{{option.depth}}</div>
                </div>
            </li>
        </ul>
        <ul v-if="loginNotFound">
            <li class="invalid-text">
                {{$localizeFilter('login_not_found')}}
            </li>
        </ul>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        options:{
            type:Array,
            required:true
        },
        loginNotFound:{
            type: Boolean,
            default: false
        }
    },
    data: ()=>({
        isOpen: false ,
        selectedIndex: null,
    }),
    setup() {
        
    },
    methods: {
        selectOption(idx) {
            this.selectedIndex = idx
            this.$emit('selectOption', idx)
            // this.isOpen = false
        }
    }
})
</script>

