<template>
    <div class="avatar-place">
        <img v-if="avatarLink && matrixUserId && !isLockedUntilTimestamp" :src="avatarLink" alt="" class="avatar" @click="navigateToUser()">
        <div v-if="!matrixUserId && !isLockedUntilTimestamp" class="add" @click="toggleContextMenu()">
            <img src="@/assets/newImages/add_button.png" alt="">
        </div>
        <div v-if="isLockedUntilTimestamp && isLockedUntilTimestamp > currTimestamp" class="lock">
            <img src="@/assets/newImages/locked_padlock.png" alt="" @click="unlockPlace()">
            <p class="countdown">{{ timeLeft }}</p>
        </div>
        <div class="context-menu" v-if="showContextMenu">
            <div class="menu-item" @click="lockPlace()">
                <img src="@/assets/newImages/unlocked_padlock_icon.png" alt="" class="icon">
                <div class="title">{{ $localizeFilter('lock_place') }}</div>
            </div>
            <div class="menu-item" @click="putClone()">
                <img src="@/assets/newImages/clone_icon.png" alt="" class="icon">
                <div class="title">{{ $localizeFilter('put_clone') }}</div>
            </div>
        </div>
        <!-- <Teleport to=".app-page.cabinet">
            <Modal v-if="showModalPutClone" :type="'confirmAction'" :actionType="'putClone'" @closeModal="closePutCloneModal"/>
            <Modal v-if="showModalLockPlace" :type="'confirmAction'" :actionType="'lockPlace'" @closeModal="closeLockPlaceModal"/>
            <Modal v-if="showModalUnlockPlace" :type="'confirmAction'" :actionType="'unlockPlace'" @closeModal="closeUnlockPlaceModal"/>
        </Teleport> -->
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from "@/filters/localize.filter";
import Modal from "@/components/modals/Modal.vue"

export default defineComponent({
  props: {
      avatarLink: String,
      isLockedUntilTimestamp: Number,
      matrixUserId: Number,
      side: String,
  },
  components: {
      Modal
  },
  data: () => ({
      showContextMenu: false,
      currTimestamp: Date.now(),
    //   showModalPutClone: false,
    //   showModalLockPlace: false,
    //   showModalUnlockPlace: false,
  }),
  methods: {
      toggleContextMenu() {
          this.showContextMenu = !this.showContextMenu;
      },
      putClone(){
          this.showContextMenu = false;
          this.$emit('putClone');
      },
      lockPlace(){
          this.showContextMenu = false;
          this.$emit('lockPlace');
      },
      unlockPlace(){
          this.showContextMenu = false;
          this.$emit('unlockPlace');
      },

    //   closePutCloneModal(confirmed){
    //       this.showContextMenu = false;
    //       this.showModalPutClone = false;
    //       if(confirmed){
    //           this.putClone();
    //       }
    //   },

    //   closeLockPlaceModal(confirmed){
    //       this.showContextMenu = false;
    //       this.showModalLockPlace = false;
    //       if(confirmed){
    //           this.lockPlace();
    //       }
    //   },

    //   closeUnlockPlaceModal(confirmed){
    //       this.showContextMenu = false;
    //       this.showModalUnlockPlace = false;
    //       if(confirmed){
    //           this.unlockPlace();
    //       }
    //   },

      navigateToUser(){
          this.$emit('pushMatrixUser', this.matrixUserId);
      }
  },
  computed: {
    timeLeft(){
        let seconds = Math.floor((this.isLockedUntilTimestamp - this.currTimestamp)/1000);
        return `${Math.floor(seconds / 60)}:${seconds % 60}`;
    }
  },
  created: function(){
      this.timerInterval = null;
  },
  mounted() { 
      this.timerInterval = setInterval(()=>{
          this.currTimestamp = Date.now();
      }, 1000)
    // window.addEventListener('scroll', this.handleScroll);
  },
  unmounted() {
      clearInterval(this.timerInterval);
  }
});
</script>

