    //  },
     changeStatus(status) {
       this.status = status
     }
  },
  computed: {
      frameWidth() {
          return Math.min(Math.min(window.innerWidth, window.screen.availWidth) - 40, 1200);
      },
      frameHeight() {
          return this.frameWidth * 0.5625;
      },
      programNumber() {
        if(this.programName == 'everyday') return 1;
        if(this.programName == 'house') return 2;
        if(this.programName == 'jump') return 3;
        if(this.programName == 'swift') return 4;
        return 1;
      },
      shortMatrix() {
        return this.programName == 'jump' || this.programName == 'swift';
      },
  }
})
